import React, { useState, useEffect } from "react";
import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  CircularProgress,
  Grid,
  Typography,
} from "@material-ui/core/";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  progress: {
    margin: "50px auto",
  },
  cardMedia: {
    paddingTop: "56.25%", // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
}));

// POSTs
const cards = [1, 2, 3, 4, 5, 6];

function Posts() {
  const classes = useStyles();
  const [phote, setPhote] = useState("");
  useEffect(() => {
    // MEMO： ボカシしたりも
    // 'https://picsum.photos/200/300/?blur=10'
    // source:  https://qiita.com/MeJamoLeo/items/9c6b4f454f5531ead0c4
    // unsplash 版
    // https://source.unsplash.com/random
    fetch("https://picsum.photos/1000/1500/").then((data) => {
      console.log("res", data);
      setPhote(data.url);
    });
  }, []);

  return (
    <>
      <Grid container spacing={4}>
        {cards.map((card) => (
          <Grid item key={card} xs={12} sm={6} md={4}>
            <Card className={classes.card}>
              {phote ? (
                <CardMedia
                  className={classes.cardMedia}
                  image={phote ? phote : "fetcing"}
                  title="Image title"
                />
              ) : (
                <CircularProgress className={classes.progress} />
              )}
              <CardContent className={classes.cardContent}>
                <Typography gutterBottom variant="h5" component="h2">
                  Title
                </Typography>
                <Typography>
                  画像を
                  <a
                    href="https://picsum.photos/"
                    target="_blank"
                    rel="noreferrer"
                  >
                    Picsum
                  </a>
                  から取得しています。
                </Typography>
              </CardContent>
              {/* <CardActions>
                <Button size="small" color="primary">
                  View
                </Button>
                <Button size="small" color="primary">
                  Edit
                </Button>
              </CardActions> */}
            </Card>
          </Grid>
        ))}
      </Grid>
      <Typography
        variant="body1"
        align="center"
        color="textSecondary"
        paragraph
      >
        使用している無料画像素材サイト
      </Typography>
      <ul>
        <li>
          <a
            href="https://unsplash.com/developers"
            target="_blank"
            rel="noreferrer"
          >
            Unsplash
          </a>
        </li>
        <li>
          ✅{" "}
          <a href="https://picsum.photos/" target="_blank" rel="noreferrer">
            Picsum
          </a>
        </li>
      </ul>
    </>
  );
}

export default Posts;
